html, body, div, span, applet, object, iframe, h1, h2, h3, h4, h5,
h6, p, blockquote, pre, a, abbr, acronym, address, big,
cite, code, del, dfn, em, img, ins, kbd, q, s, samp, small,
strike, strong, sub, sup, tt, var, b, u, i, center, dl, dt,
dd, ol, ul, li, fieldset, form, label, legend, table, caption,
tbody, tfoot, thead, tr, th, td, article, aside, canvas, details,
embed, figure, figcaption, footer, header, hgroup, menu, nav,
output, ruby, section, summary, time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  vertical-align: baseline;
}

html {
  -ms-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
}

img, fieldset, a img {
  border: none;
}

input[type="text"],
input[type="email"],
input[type="tel"],
textarea {
  -webkit-appearance: none;
}

input[type="submit"],
button {
  cursor: pointer;
}

input[type="submit"]::-moz-focus-inner,
button::-moz-focus-inner {
  padding: 0;
  border: 0;
}

textarea {
  overflow: auto;
}

input, button {
  margin: 0;
  padding: 0;
  border: 0;
}

div, input, textarea, select, button,
h1, h2, h3, h4, h5, h6, a, span, a:focus {
  outline: none;
}

ul, ol {
  list-style-type: none;
}

table {
  border-spacing: 0;
  border-collapse: collapse;
  width: 100%;
}

html {
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

@media (prefers-reduced-motion: reduce) {
  * {
    transition: none !important;
  }
}

.icon-facebook {
  width: 0.53em;
  height: 1em;
  fill: #000;
}

.block {
  display: flex;
}

* {
  margin: 0;
  padding: 0;
}

.container {
  width: 940px;
  margin: auto;
  margin-background: #FFF url(../img/img/bg.png) no-repeat center;
}

.header__menu {
  float: right;
  padding-top: 35px;
}

.header__menu ul li {
  cursor: pointer;
  display: inline;
  margin-right: 51px;
}

.header__menu ul li:nth-child(5) {
  margin-right: 0px;
}

.header__menu ul li a {
  color: black;
  text-decoration: none;
  font-family: sans-serif;
  font-size: 18px;
}

.h2 {
  width: 47px;
  height: 23px;
  color: #ffffff;
  font-family: Bebas;
  font-size: 24px;
  font-weight: 400;
  padding-top: 19px;
  padding-left: 19px;
}

.header__logo {
  float: left;
  width: 87px;
  height: 34px;
  color: #e8603c;
  font-family: Bebas;
  font-size: 36px;
  font-weight: 400;
  text-transform: uppercase;
}

.background_rectangles {
  padding-top: 78px;
  padding-bottom: 30px;
}

.center__rectangle {
  width: 940px;
  height: 61px;
  background-color: #e8603c;
  margin: auto;
}

.left__rectangle {
  float: left;
}

.right__rectangle {
  float: right;
}

.side__rectangle {
  width: 180px;
  height: 61px;
  background-color: rgba(232, 96, 60, 0.5);
}

.post__statistic {
  float: left;
}

.small__red__text {
  font-size: 16px;
}

.small__grey__text {
  font-size: 16px;
}

.blog__block {
  position: relative;
  width: 100%;
  height: auto;
  padding-bottom: 30px;
}

.blog__icon {
  padding-top: 20px;
  position: absolute;
  left: -90px;
  height: 75px;
  width: 75px;
  background-color: #e8603c;
  text-align: center;
}

.post__text {
  padding-left: 150px;
  width: 670px;
}

.article {
  color: #e8603c;
  font-family: Bebas;
  font-size: 18px;
}

.post__text__info {
  font-size: 12px;
}

.dots__line {
  padding-top: 20px;
}

.blog__small__img {
  position: absolute;
  top: 20px;
  left: 34px;
  height: 75px;
  float: left;
}

.blog__first__topic {
  float: left;
  padding-bottom: 30px;
}

.quote__text__info {
  padding-top: 25px;
  padding-left: 70px;
  font-size: 13px;
  width: 670px;
}

.quote__block {
  background-color: #f5f5f5;
  width: 670px;
}

.link__text {
  padding: 0;
}

.paggination {
  padding-bottom: 20px;
}

.paggination ul li {
  display: inline-block;
  text-align: center;
  height: 26px;
  width: 26px;
  background-color: #f3b29d;
  cursor: pointer;
  margin-right: 20px;
}

.paggination ul li:hover {
  background-color: #e8663c;
}

.paggination ul li:active {
  background: #e8663c;
}

.paggination ul li a {
  color: black;
  text-decoration: none;
  font-family: sans-serif;
  font-size: 18px;
}

.h3 {
  padding-top: 2px;
}

.block__sidebar {
  display: block;
  float: right;
  width: 25%;
}

.gallery__photo {
  width: 50px;
  height: 50px;
  border: 1px solid #e8603c;
  background-color: #f3af9d;
  display: inline-block;
  margin-right: 5px;
}

